.itemInside {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.itemInsideHead {
    background-color: aquamarine;
    display: flex;
    width: 100%;
    justify-content: space-between;
    padding-top: 40px;
}

.backlink {
    text-decoration: none;
    background-color: aliceblue;
    margin: 0 10px;
    /* margin-bottom: 20px; */
    padding: 15px;
    border-radius: 16px;
}

.tomemorisbutton {
    text-decoration: none;
    background-color: rgb(77, 172, 255);
    color: aliceblue;
    padding: 15px;
    border-radius: 16px;
